import React from "react";
import styles from "./styles.module.scss";
import classNames from "classnames/bind";
import TitlePresenter from "Components/Title";
import { Link } from "react-router-dom";

const cx = classNames.bind(styles);

const Contents = ({ qnasByTerm, _goBack }) => {
  return (
    <div className={cx("container")}>
      <div className={cx("header")}>
        <TitlePresenter title={" "} subTitle={"Q&A"} />
      </div>
      <div className={cx("body")}>
        <div className={cx("title")}>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
        <div className={cx("contents")}>
          {qnasByTerm.map(qna => {
            return (
              <ContentItem
                key={qna.id}
                num={qna.id}
                title={qna.title}
                date={qna.updatedAt}
                views={qna.views}
              />
            );
          })}
        </div>
      </div>
      <div className={cx("bottom")}>
        <button onClick={_goBack}></button>
      </div>
    </div>
  );
};

const ContentItem = ({ num, title, date, views }) => {
  return (
    <div className={cx("ContentItem-container")}>
      <span>{num}</span>
      <span>
        <Link className={cx("link")} to={`/question&answer-detail/${num}`}>
          {title}
        </Link>
      </span>
      <span>{date.substring(0, 10)}</span>
      <span>{views}</span>
    </div>
  );
};

export default Contents;
