import React from "react";
import styles from "./styles.module.scss";
import classNames from "classnames/bind";
import TitlePresenter from "Components/Title";
import { Link } from "react-router-dom";

const cx = classNames.bind(styles);

const Contents = ({ faqsByTerm, _goBack }) => {
  return (
    <div className={cx("container")}>
      <div className={cx("header")}>
        <TitlePresenter title={"  "} subTitle={"FAQ"} />
      </div>
      <div className={cx("body")}>
        <div className={cx("title")}>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
        <div className={cx("contents")}>
          {faqsByTerm.map(faq => {
            return (
              <ContentItem
                num={faq.id}
                key={faq.id}
                title={faq.title}
                date={faq.updatedAt.substring(0, 10)}
                views={faq.views}
              />
            );
          })}
        </div>
      </div>
      <div className={cx("bottom")}>
        <button onClick={_goBack}></button>
      </div>
    </div>
  );
};

const ContentItem = ({ num, title, date, views }) => {
  return (
    <div className={cx("ContentItem-container")}>
      <span>{num}</span>
      <span>
        <Link
          className={cx("link")}
          style={{ textDecoration: "none" }}
          to={`/frequently-asked-detail/${num}`}
        >
          {title}
        </Link>
      </span>
      <span>{date}</span>
      <span>{views}</span>
    </div>
  );
};

export default Contents;
